import React, { useState, useEffect } from "react";
import { randomFrom0To5 } from "../../util/randomFrom0To5";
import { useLocalStorage } from "../../hooks/useLocalStorage";
import TopInfo from "../TopInfo/TopInfo";
import { Container, StyledBoard, Play } from "./Board.style";
import SingleBulb from "../SingleBulb/SingleBulb";
import { colors, notes } from "../../data/color_note";

const Board = () => {
  const [userMode, setUserMode] = useState(false);
  const [selectedColor, setSelectedColor] = useState("");
  const [playArr, setPlayArr] = useState([]);
  const [userArr, setUserArr] = useState([]);
  const [noSound, setNoSound] = useState(false);
  const [bestScore, setBestSCore] = useLocalStorage(
    "bestScore",
    playArr.length * 10
  );

  useEffect(() => {
    if ((playArr.length - 1) * 10 > Number(bestScore)) {
      if (playArr.length === 0) {
        setBestSCore(0);
      } else {
        setBestSCore((playArr.length - 1) * 10);
      }
    }
  }, [playArr.length]);

  const play = () => {
    setUserArr([]);
    const newArr = [...playArr];
    let randomIdx = randomFrom0To5();
    randomIdx =
      randomIdx === newArr[newArr.length - 1] ? randomFrom0To5() : randomIdx;
    newArr.push(colors[randomIdx]);
    setPlayArr(newArr);
    run(newArr);
  };

  const run = (arr) => {
    setUserMode(false);
    arr.map((item, idx) =>
      setTimeout(() => {
        setTimeout(() => {
          setSelectedColor("");
        }, 300);
        setSelectedColor(item);
        !noSound && notes[colors.indexOf(item)].play();
        setUserMode(true);
      }, 1000 * idx)
    );
  };

  const reset = () => {
    setPlayArr([]);
  };

  const click = (idx) => {
    !noSound && notes[idx].play();

    const newArr = [...userArr];
    newArr.push(colors[idx]);
    setUserArr((prev) => [...prev, colors[idx]]);
    validattion(newArr, newArr[newArr.length - 1], newArr.length - 1);
  };
  const validattion = (newArr, item, index) => {
    if (item !== playArr[index]) {
      !noSound && notes[0].play();
      setUserMode(false);
      setUserArr([]);
      reset();
    } else {
      if (newArr.length === playArr.length) {
        setTimeout(() => {
          setUserMode(false);
          play();
        }, 800);
      }
    }
  };

  const BulbsList = colors?.map((bulb, idx) => {
    const on = selectedColor === bulb;
    return (
      <SingleBulb
        key={idx}
        note={notes[idx]}
        bulb={bulb}
        idx={idx}
        on={on}
        click={click}
        userMode={userMode}
      />
    );
  });

  return (
    <Container>
      <TopInfo
        noSound={noSound}
        toggleSound={() => setNoSound((prev) => !prev)}
        bestScore={bestScore}
        currentScore={
          (playArr.length - 1) * 10 < 0 ? 0 : (playArr.length - 1) * 10
        }
      />
      <StyledBoard>
        {BulbsList}
        {playArr.length === 0 && (
          <Play>
            <div onClick={play}> &#9658;</div>
          </Play>
        )}
      </StyledBoard>
    </Container>
  );
};

export default Board;
